import React from "react";

function Modal({ onClose, currentProject }) {
  const {
    name,
    category,
    description,
    url,
    github,
    index,
    technologies,
  } = currentProject;

  return (
    <div className="modalBackdrop">
      <div className="modalContainer">
        <h1 className="modalTitle">{name}</h1>
        <img
          src={require(`../assets/img/large/${category}/${index}.jpg`).default}
          alt="current category"
        />
        <p>{description}</p>
        <h3>Technologies Used</h3>
        <hr></hr>
        <p>{technologies}</p>
        <a href={url}>
          <p>{url}</p>
        </a>
        <a href={github}>
          <p>{github}</p>
        </a>
        <button onClick={onClose} type="button">
          Close this modal
        </button>
      </div>
    </div>
  );
}

export default Modal;
