import React from "react";
import resume from "../assets/img/Resume.jpg";

function Resume() {
  return (
    <section className="my-5">
      <img
        src={resume}
        className="my-2"
        style={{ width: "35%" }}
        alt="resume"
      />
    </section>
  );
}

export default Resume;
