import React from "react";
import myPic from "../assets/img/will-photo.jpg";

function About() {
  return (
    <section className="my-5">
      <h1 id="about">About Me</h1>
      <img
        src={myPic}
        className="my-2"
        style={{ width: "35%" }}
        alt="profilePic"
      />
      <div className="my-2">
        <p>
          With our world changing rapidly, I decided it was time to try
          something new. I particularily enjoy solving puzzles and strategy
          games, so coding is a natural fit for me. Coding is very interesting
          to me because it allows me to use my creativity in new ways. I am
          passionate about problem solving and learning and using new skills.
        </p>
        <h1>Technical Skills</h1>
        <p>
          Mongoose, MongoDB, Express, React.js, Node.js, HTML, CSS, GitHub,
          JavaScript, InDesign, Photoshop, Illustrator, Figma
        </p>
      </div>
    </section>
  );
}

export default About;
